import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import Pagination from '../Base/Paginations/Pagnations';
import { getAllWards } from '../Apis';

//import usersData from './UsersData'
const addward = 'ward/add'


function WardRow(props) {
  const wards = props.wards
  const wardLink = `/wards/${wards.id}`
  const updateward = `ward/update/${wards.id}`
 

  const getBadge = (status) => {
    return status === 'Active' ? 'success' :
      status === 'Inactive' ? 'secondary' :
        status === 'Pending' ? 'warning' :
          status === 'Banned' ? 'danger' :
            'primary'
  }

  return (
    <tr key={wards.id.toString()}>
      <th scope="row"><Link to={wardLink}>{wards.id}</Link></th>
      <td><Link to={wardLink}>{wards.name}</Link></td>
      <td>{wards.description}</td>
      <td>{wards.ward_no}</td>
      <td><Link to={updateward}><span class="cui-pencil" aria-hidden="true"></span></Link></td>
    </tr>
  )
}

class Wards extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      totalPages: 0,
      totalRecords: 0
    };
  }

  componentDidMount() {
    this.getAllWards(1)
  
  }
  getAllWards=async(page)=>{
    const responseData= await getAllWards(page);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
              isLoaded: true,
              items: responseData.data.result,
              totalPages: responseData.data.pages,
              totalRecords: responseData.data.count
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  onPageChanged = data => {
    //const { allCountries } = this.state;
   
    const { currentPage, totalPages, pageLimit } = data;
    this.getAllWards(currentPage)
    console.log(data);
  };

  render() {

    const wardList = this.state.items; //usersData.filter((user) => user.id < 10)
    const Totrecords = parseInt(this.state.totalRecords); // total users records.
    let pagination;

    if(Totrecords !== 0 ){
      pagination = <Pagination totalRecords={Totrecords} pageLimit={5} pageNeighbours={1} onPageChanged={this.onPageChanged} />
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xl={3}>
          </Col>
          <Col xl={6}>
            <Card>
              <CardHeader>
                <i className="fa fa-align-justify"></i> Wards 
                <Link to={addward}><button onClick={this.showHospitalInfo} className='btn btn-success  userbtn'>+ Add Ward</button></Link>
              </CardHeader>
              <CardBody>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">Id</th>
                      <th scope="col">Name</th>
                      <th scope="col">Description</th>
                      <th scope="col">Ward No</th>
                      <th scope="col">Edit Ward</th>
                    </tr>
                  </thead>
                  <tbody>
                    {wardList.map((wards, index) =>
                      <WardRow key={index} wards={wards}/>
                    )}
                  </tbody>
                </Table>
              </CardBody>              
              {pagination}
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default Wards;
